.bracket {
  max-width: 650px;
}
.bracket::after {
  width: 20px;
  height: 100%;
  position: absolute;
  top: 0;
  right: -20px;
  box-shadow: 0 30px 40px rgb(0 0 0 / 50%);
}

.round {
  max-width: 550px;
}

.section {
  min-width: 360px;
}

.rd16 .section {
  height: 360px;
}
.rd16 .section .merger {
  height: 194px;
}

.quarterfinals .section {
  height: 748px;
}
.quarterfinals .section .matchups,
.quarterfinals .section .connector {
  margin: 96px 0;
}
.quarterfinals .section .merger {
  height: 390px;
}

.semifinals .section {
  height: 1524px;
}
.semifinals .section .matchups,
.semifinals .section .connector {
  margin: 290px 0;
}
.semifinals .section .merger {
  height: 778px;
}

.final .section {
  height: 1524px;
}
.final .section .matchups {
  margin: 678px 0;
}
.final .section .connector {
  display: none;
}
